.App {
  text-align: center;
  background-color: red;
}
.main-div{
  background-image: linear-gradient(to right top, #808080, #9a9a9a, #b4b4b4, #d0d0d0, #ececec);
  min-height: 100vh;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #4f638b;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

figure {
  display: flex;
  justify-content: center;
  align-items: center;
  
}
figure img {
  width: 200px; 
  height: 200px; 
  color: blanchedalmond;
}

