.container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  background-color: rgba(242, 242, 242, 0.8);
  border-radius: 7px;
}

  
  .form-label {
    font-weight: bold;
  }
  
  .form-control {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 7px;
  }
  
  .btn-success {
    background-color: green;
    color: white;
  }
  